.pbody {
    font-family: "Montserrat", sans-serif;
    min-height: 100vh;
    max-height: 100vh;
    margin: 0;
    color: #fff;
    display: flex;
    flex-direction: column;
    background: #222222;
    background: -webkit-linear-gradient(to top, #232526, #414345);
}

.pbody a {
    color: inherit;
}

.pbody a:not(:hover) {
    text-decoration: none;
}

.pbody #link-a-me {
    position: absolute;
    bottom: 80px;
    right: 20px;
    height: 60px;
    width: 60px;
    background-size: cover;
    z-index: 1;
    animation: pop-in 120s ease-out;
}

.pbody .content {
    overflow: hidden;
    flex-grow: 1;
    height: calc(100% - 80px);
}

.pbody .content .lyrics {
    padding: 50vh 0;
    min-height: calc(100vh - 80px);
    width: calc(100% - 40px);
    margin: 0 20px;
    text-align: center;
    transition: all 0.25s;
    position: relative;
}

.pbody .content .lyrics>div {
    position: relative;
    font-size: 40px;
    line-height: 80px;
    color: #666;
    transition: all 0.25s;
}

.pbody .content .lyrics>div:before {
    content: attr(note);
    position: absolute;
    top: 0px;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 18px;
}

.pbody .content .lyrics>div.highlighted {
    color: #fff;
    font-size: 48px;
}

.pbody .player {
    z-index: 10;
    background: #F9F9F9;
    color: #000;
    min-height: 80px;
    max-height: 80px;
    display: flex;
    flex-direction: row;
}

.pbody .player .left {
    width: 80px;
    background-size: cover;
}

.pbody .player .right {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

.pbody .player .right .top {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 10px;
}

.pbody .player .right .top .song {
    font-size: 1.2em;
    font-weight: 600;
}

.pbody .player .right .bottom {
    display: flex;
}

.pbody .player .right .bottom video {
    flex-grow: 1;
    height: 32px;
}

@keyframes pop-in {

    0%,
    95% {
        transform: translateY(100%);
    }

    100% {
        transform: translateY(0%);
    }
}

.score{
    display: inline-block;
    background-color: #787878;
    width: 50vw;
    height: 70vh;
    border-radius: 10px;
    position: absolute;
    top: 50%;
    left: 50%;
    text-align: center;
    justify-content: center;
    transform: translate(-50%, -50%);
  }
  
  .scoreDisplay{
    margin-top: 90px;
    font-size: 50px;
    font-weight: 700;
    color: #fff;
  }
  
  .scoreaBoardNavBtn{
    font-size: 20px;
    font-weight: 700;
    color: #fff;
    margin-top: 45vh;
    text-decoration: none;
  }
  .scoreaBoardNavBtn button{
    margin: 20px;
    padding: 10px;
    border-radius: 10px;
    border: none;
    font-weight: 800;
  }
  .backButton {
    margin-top: 20px;
    margin-left: 20px;
    align-items: center;
    background-color: #118bee;
    padding: 10px 15px 5px 15px;
    border-radius: 5px;
  }
  .icons {
    font-size: 20px;
  }